.about{
    padding: 50px 140px;
}
.componentTitle{
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 2px;
    color: rgba(33, 33, 33, 1);

}
.description{
    font-family: 'Lato', sans-serif;
    color: rgba(100, 100, 100, 1);
    font-size: 16px;
    line-height: 24px;
}
.box{
    width:100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
}

.descriptionContainer{
    margin-bottom: 50px;
    flex-basis: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.imageContainer{
    flex-basis: 30%;
    position: relative;
    display: flex;
    justify-content: flex-end;
}
.image{
    position: absolute;
    top: -20px;
    right: 20px;
    width: 250px;
    box-sizing: border-box;
    border-radius: 108px 0px;
}
.imageFrame{
    width: 250px;
    height: 360px;
    border: 2px solid #3F593D;
    box-sizing: border-box;
    border-radius: 110px 0px
}
.button{
    margin:20px 0;
    width:190px;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    color: rgba(255, 255, 255, 1);
    background: rgba(8, 52, 137, 0.82);
    border-radius: 15px 0px;
    text-transform: uppercase;
    text-decoration: none;
    border: none;
    font-size: 1rem;
    padding-top: 8px;
    padding-bottom: 8px;
    cursor: pointer;
    align-self: flex-end;
    position: relative;
    z-index: 5;
    transition: all 0.3s ease;
}
.button:hover{
    background: rgba(9, 41, 105, 0.82);

}
.subtitle{
    margin-top: 50px;
    color: rgba(100, 100, 100, 1);
}
.technologiesContainer{
    margin: 40px 0 15px;
    display: flex;
    justify-content: space-around;
    padding: 30px 10px;
    background: #FFFFFF;
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.14);
    border-radius: 35px 0px;
}

.logoContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.logoImg{
    height: 35px;
}
.logoName{
    font-family: 'Lato', sans-serif;
    margin: 5px 0 0 0;
    color:#646464;
    text-align: center;
}
.factsContainer{
    margin: 40px 0 15px;
    display: flex;
    justify-content: space-between;
}
.factBox{
    padding: 30px 10px;
    flex-basis: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #FFFFFF;
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.14);
    border-radius: 5px;
}
.factImg{
    height: 40px;
}
.imageContainerMobile{
    display: none;
}
@media (max-width: 1023px){
    .about{
        padding: 70px 25px 20px;
    }
    .componentTitle{
    text-align: center;
    }
    .box{
        width: 100%;
        display: flex;
        /* margin-bottom: 50px; */
        flex-direction: column;
    }
    .descriptionContainer{
        margin-bottom:0;
    }
    .imageContainer{
        flex-basis: auto;
        display: none;
    }
    .imageContainerMobile{
        display: flex;
        margin-top: 20px;
        position: relative;
        align-self: center;
    }
    .image{
        position: absolute;
        top: -10px;
        right: 10px;
        width: 125px;
        box-sizing: border-box;
        border-radius: 54px 0px;
    }
    .imageFrame{
        width: 125px;
        height: 180px;
        border: 2px solid #3F593D;
        box-sizing: border-box;
        border-radius: 54px 0px
    }
    .button{
        margin: 20px auto;
    }

    .technologiesContainer{
        display: grid;
        grid-template-columns: 1fr 1fr;
        justify-items: center;
        row-gap: 30px;
    }
    .factsContainer{
        display: flex;
        flex-direction: column;
        row-gap: 12px;
    }
    .factBox{
        padding: 20px 30px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .factImg{
        display: block;
        flex-basis: 20%;
    }
    .factBox .logoName{
        flex-basis: 70%;
        text-align: left;
    }
}